const UrlData = {
  DEPARTMENT_CARS: `https://crm.atlantm.com/esoft/test-drive/requests/departmentsandcarshttp.php`,
  LOGBOOK_CARS: `https://crm.atlantm.com/esoft/test-drive/requests/highloadhttp.php`,
}

const ErrorXhr = {
  OK: 200,
  BAD_REQUEST: 400,
  UNAUTHORIZED: 401,
  NOT_FOUND: 404,
}

const getXhr = (onSuccess, onError) => {
  const xhr = new XMLHttpRequest()
  xhr.responseType = `json`

  xhr.addEventListener(`load`, () => {
    let error
    switch (xhr.status) {
      case ErrorXhr.OK:
        onSuccess(xhr.response)
        break

      case ErrorXhr.BAD_REQUEST:
        error = `Неверный запрос`
        break
      case ErrorXhr.UNAUTHORIZED:
        error = `Пользователь не авторизован`
        break
      case ErrorXhr.NOT_FOUND:
        error = `Ничего не найдено`
        break

      default:
        error = `Cтатус ответа: : ` + xhr.status + ` ` + xhr.statusText
    }

    if (error) {
      onError(error)
    }
  })

  xhr.addEventListener(`error`, () => {
    onError(`Произошла ошибка соединения`)
  })
  return xhr
}

const loadDepartmentCars = (onSuccess, onError) => {
  if (isDev) {
    return onSuccess(STATIC_DATE.departmentSandCars)
  } else {
    const xhr = getXhr(onSuccess, onError)

    xhr.open(`GET`, UrlData.DEPARTMENT_CARS)
    xhr.send()
  }
}

const loadLogbookCars = (onSuccess, onError, date) => {
  if (isDev) {
    return onSuccess(STATIC_DATE.dddd)
  } else {
    const xhr = getXhr(onSuccess, onError)

    xhr.open(`GET`, `${UrlData.LOGBOOK_CARS}?date=${date}`)
    xhr.send()
  }
}

